<template>
  <section class="main">
    <div class="container">
      <div class="main__wrap">
        <Products v-if="products" />
        <div class="loading-overlay" v-else>
          <div class="loading-circle"></div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import Products from "@/components/Product/Products.vue";
import { mapState } from "vuex";

export default {
  components: {
    Products,
  },
  methods: {
    getProducts() {
      this.$store.dispatch("getProducts");
    },
  },
  computed: {
    ...mapState(["products"]),
  },
  created() {
    this.getProducts();
  },
};
</script>

