<template>
  <div
    class="product"
    v-for="product in searchResults || products"
    :key="product.id"
    @click="showProductDetails(product)"
  >
    <div class="product__wrap">
      <img :src="convertImageUrl(product.image.url)" alt="" class="product__img" />
      <div class="product__desc">
          <h1 class="product__h">{{ product.name }}</h1>
          <h1 class="product__price">{{ product.price.formatted }}0 Сум</h1>
      </div>
    </div>
  </div>
  <Product
    v-if="selectedProduct"
    :product="selectedProduct"
    @back="clearSelectedProduct"
  />
</template>

<script>
import { mapState, mapActions } from "vuex";
import Product from "./Product.vue";
export default {
  components: {
    Product,
  },
  created() {
    this.getCart();
  },
  computed: {
    ...mapState([
      "products",
      "cart",
      "addingToCart",
      "selectedProduct",
      "searchResults",
    ]),
  },
  methods: {
    ...mapActions([
      "getCart",
      "addToCart",
      "selectProduct",
      "clearSelectedProduct",
    ]),
    showProductDetails(product) {
      this.selectProduct(product);
    },
    convertImageUrl(url) {
      return url.replace(/\|/g, '%7C');
    },
  },
};
</script>

